'use client'

import React, { useState } from 'react';
import { ArrowUpRight, Thermometer, Droplets, Menu, X, MapPin } from 'lucide-react';

// Mock data for demonstration
const mockData = {
  date: "11/05/2024",
  mainReading: {
    value: "0.18",
    unit: "µg/m³",
    status: "Good",
    note: "Long-term/Short-term"
  },
  conditions: {
    temperature: "26°C",
    humidity: "75%"
  },
  pmReadings: [
    { type: "PM0.1", value: "0.18", unit: "µg/m³", description: "อนุภาคขนาดเล็กกว่า 0.1 ไมครอน" },
    { type: "PM2.5", value: "15.99", unit: "µg/m³", description: "อนุภาคขนาดเล็กกว่า 2.5 ไมครอน" },
    { type: "PM10", value: "13.67", unit: "µg/m³", description: "อนุภาคขนาดเล็กกว่า 10 ไมครอน" }
  ],
  recommendations: [
    "ไม่มีผลกระทบต่างแก่",
    "สามารถใช้ชีวิตได้ตามปกติ",
    "สามารถออกกำลังกายกลางแจ้งได้"
  ]
};

const getAirQualityColor = (status) => {
  switch (status.toLowerCase()) {
    case 'good': return '#3b82f6';
    case 'moderate': return '#eab308';
    case 'unhealthy': return '#f97316';
    case 'hazardous': return '#dc2626';
    default: return '#6b7280';
  }
};

const formatPMValue = (value) => `${value} µg/m³`;

const Header = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  return (
    <header className="h-16 bg-green-100 border-b border-green-200 flex-shrink-0">
      <div className="h-full flex items-center justify-between px-6">
        <div className="flex items-center gap-4">
          <div className="w-12 h-12 flex-shrink-0 flex items-center justify-center">
            <img
              src="/api/placeholder/48/48"
              alt="Mupcop logo"
              className="w-10 h-10 object-contain rounded-lg"
            />
          </div>
          <div>
            <h1 className="text-xl font-bold text-gray-800">Mupcop</h1>
            <h2 className="text-xs font-light text-gray-600 max-w-xl">
              Multi-factor ultrafine particle (PM01) concentration optimization prediction
            </h2>
          </div>
        </div>

        <nav className="hidden md:flex items-center gap-1">
          <button className="px-4 py-2 text-sm font-medium text-gray-700 bg-white rounded-lg shadow-sm border border-gray-200 hover:bg-gray-50 transition-colors">
            Air quality
          </button>
          <button className="px-4 py-2 text-sm font-medium text-gray-600 rounded-lg hover:bg-green-200 transition-colors">
            History data
          </button>
        </nav>

        <button
          className="md:hidden p-2 rounded-lg hover:bg-green-200 transition-colors"
          onClick={() => setIsMenuOpen(!isMenuOpen)}
        >
          {isMenuOpen ? <X size={20} /> : <Menu size={20} />}
        </button>
      </div>
    </header>
  );
};

const MonitoringPanel = () => {
  const data = mockData;
  const statusColor = getAirQualityColor(data.mainReading.status);

  return (
    <div className="w-80 bg-gray-50 border-r border-gray-200 overflow-auto">
      <div className="p-4">
        <h2 className="text-lg font-semibold mb-4 text-gray-800">UFPs Monitoring</h2>

        {/* Live status */}
        <div className="bg-white rounded-lg p-4 shadow-sm border border-gray-100 mb-4">
          <div className="flex items-center justify-between mb-3">
            <div className="flex items-center gap-2">
              <div className="w-2 h-2 bg-green-500 rounded-full animate-pulse" />
              <span className="text-sm font-semibold text-gray-800">LIVE</span>
            </div>
            <span className="text-xs text-gray-500">{data.date}</span>
          </div>

          {/* Main reading */}
          <div
            className="p-4 rounded-lg mb-4 text-white"
            style={{ backgroundColor: statusColor }}
          >
            <div className="flex justify-between items-start">
              <div>
                <span className="text-2xl font-bold">{data.mainReading.value}</span>
                <span className="text-sm ml-1 opacity-90">{data.mainReading.unit}</span>
              </div>
              <div className="text-right">
                <div className="text-sm font-semibold">{data.mainReading.status}</div>
                <div className="text-xs opacity-80">{data.mainReading.note}</div>
              </div>
            </div>
          </div>

          {/* Environmental conditions */}
          <div className="grid grid-cols-2 gap-4">
            <div className="text-center">
              <Thermometer className="w-5 h-5 text-gray-400 mx-auto mb-1" />
              <div className="text-sm text-gray-600">{data.conditions.temperature}</div>
            </div>
            <div className="text-center">
              <Droplets className="w-5 h-5 text-gray-400 mx-auto mb-1" />
              <div className="text-sm text-gray-600">{data.conditions.humidity}</div>
            </div>
          </div>
        </div>

        {/* PM Readings */}
        <div className="bg-white rounded-lg p-4 shadow-sm border border-gray-100 mb-4">
          <h3 className="text-sm font-semibold text-gray-800 mb-3">มลพิษอนุภาคฝุ่น</h3>
          <div className="space-y-3">
            {data.pmReadings.map((reading, index) => (
              <div key={`pm-${index}`} className="flex items-center justify-between">
                <div>
                  <div className="flex items-center gap-2">
                    <div className="w-2 h-2 bg-blue-500 rounded-full" />
                    <span className="text-sm font-medium text-gray-800">{reading.type}</span>
                  </div>
                  <div className="text-xs text-gray-500 ml-4">{reading.description}</div>
                </div>
                <div className="text-right">
                  <span className="text-sm font-semibold text-blue-600">
                    {formatPMValue(reading.value)}
                  </span>
                </div>
              </div>
            ))}
          </div>
        </div>

        {/* Recommendations */}
        <div className="bg-white rounded-lg p-4 shadow-sm border border-gray-100">
          <h3 className="text-sm font-semibold text-gray-800 mb-3">คำแนะนำวันนี้</h3>
          <div className="space-y-2">
            {data.recommendations.map((recommendation, index) => (
              <div
                key={`rec-${index}`}
                className="flex items-start gap-2 text-sm text-gray-600"
              >
                <ArrowUpRight className="w-4 h-4 text-green-500 mt-0.5 flex-shrink-0" />
                <span>{recommendation}</span>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
};

const MapView = () => {
  // Mock monitoring stations
  const stations = [
    { id: 1, name: "สถานีตรวจวัดกลางเมือง", lat: 13.7563, lng: 100.5018, value: "0.18", status: "good" },
    { id: 2, name: "สถานีตรวจวัดชานเมือง", lat: 13.7263, lng: 100.5418, value: "0.25", status: "moderate" },
    { id: 3, name: "สถานีตรวจวัดอุตสาหกรรม", lat: 13.7863, lng: 100.4818, value: "0.32", status: "unhealthy" },
    { id: 4, name: "สถานีตรวจวัดยานยนต์", lat: 13.7163, lng: 100.5218, value: "0.21", status: "good" },
    { id: 5, name: "สถานีตรวจวัดที่อยู่อาศัย", lat: 13.7663, lng: 100.5618, value: "0.15", status: "good" }
  ];

  const getStatusColor = (status) => {
    switch (status) {
      case 'good': return 'bg-green-500';
      case 'moderate': return 'bg-yellow-500';
      case 'unhealthy': return 'bg-orange-500';
      case 'hazardous': return 'bg-red-500';
      default: return 'bg-gray-500';
    }
  };

  return (
    <div className="flex-1 relative bg-green-50">
      {/* Map background */}
      <div className="absolute inset-0 bg-gradient-to-br from-green-100 to-green-50">
        {/* Grid pattern to simulate map */}
        <div className="absolute inset-0 opacity-20">
          <svg width="100%" height="100%" className="absolute inset-0">
            <defs>
              <pattern id="grid" width="50" height="50" patternUnits="userSpaceOnUse">
                <path d="M 50 0 L 0 0 0 50" fill="none" stroke="#22c55e" strokeWidth="1" opacity="0.3" />
              </pattern>
            </defs>
            <rect width="100%" height="100%" fill="url(#grid)" />
          </svg>
        </div>

        {/* Roads simulation */}
        <div className="absolute inset-0">
          <div className="absolute top-1/3 left-0 right-0 h-4 bg-gray-300 opacity-30"></div>
          <div className="absolute top-2/3 left-0 right-0 h-6 bg-gray-400 opacity-40"></div>
          <div className="absolute top-0 bottom-0 left-1/4 w-4 bg-gray-300 opacity-30"></div>
          <div className="absolute top-0 bottom-0 right-1/3 w-6 bg-gray-400 opacity-40"></div>
        </div>

        {/* Monitoring stations */}
        {stations.map((station) => (
          <div
            key={station.id}
            className="absolute transform -translate-x-1/2 -translate-y-1/2 cursor-pointer"
            style={{
              left: `${(station.lng - 100.4518) * 100 / 0.15}%`,
              top: `${(13.8063 - station.lat) * 100 / 0.15}%`
            }}
          >
            <div className="relative group">
              {/* Station marker */}
              <div className={`w-4 h-4 rounded-full ${getStatusColor(station.status)} border-2 border-white shadow-lg`}>
                <div className="absolute inset-0 rounded-full animate-ping bg-current opacity-30"></div>
              </div>

              {/* Station info popup */}
              <div className="absolute bottom-6 left-1/2 transform -translate-x-1/2 bg-white rounded-lg shadow-lg border border-gray-200 p-3 min-w-48 opacity-0 group-hover:opacity-100 transition-opacity duration-200 z-10">
                <div className="text-sm font-semibold text-gray-800 mb-1">{station.name}</div>
                <div className="text-xs text-gray-600 mb-2">PM0.1: {station.value} µg/m³</div>
                <div className={`inline-flex items-center gap-1 px-2 py-1 rounded-full text-xs font-medium text-white ${getStatusColor(station.status)}`}>
                  <div className="w-1.5 h-1.5 rounded-full bg-white"></div>
                  {station.status === 'good' ? 'ดี' : station.status === 'moderate' ? 'ปานกลาง' : 'ไม่ดี'}
                </div>
                {/* Arrow pointing down */}
                <div className={`absolute top-full left-1/2 transform -translate-x-1/2 w-0 h-0 border-l-4 border-r-4 border-t-4 border-transparent border-t-white`}></div>
              </div>
            </div>
          </div>
        ))}

        {/* Map legend */}
        <div className="absolute top-4 right-4 bg-white rounded-lg shadow-sm border border-gray-200 p-3">
          <h4 className="text-sm font-semibold text-gray-800 mb-2">สถานะคุณภาพอากาศ</h4>
          <div className="space-y-2">
            <div className="flex items-center gap-2">
              <div className="w-3 h-3 rounded-full bg-green-500"></div>
              <span className="text-xs text-gray-600">ดี (0-12 µg/m³)</span>
            </div>
            <div className="flex items-center gap-2">
              <div className="w-3 h-3 rounded-full bg-yellow-500"></div>
              <span className="text-xs text-gray-600">ปานกลาง (13-35 µg/m³)</span>
            </div>
            <div className="flex items-center gap-2">
              <div className="w-3 h-3 rounded-full bg-orange-500"></div>
              <span className="text-xs text-gray-600">ไม่ดี (36+ µg/m³)</span>
            </div>
          </div>
        </div>

        {/* Current location indicator */}
        <div className="absolute bottom-4 left-4 bg-white rounded-lg shadow-sm border border-gray-200 p-3">
          <div className="flex items-center gap-2">
            <MapPin className="w-4 h-4 text-blue-500" />
            <div>
              <div className="text-sm font-semibold text-gray-800">ตำแหน่งปัจจุบัน</div>
              <div className="text-xs text-gray-600">กรุงเทพมหานคร</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

const Footer = () => (
  <footer className="bg-green-100 border-t border-green-200 px-6 py-3">
    <div className="flex items-center justify-between">
      <span className="text-sm text-gray-600">Made by Jaejae Dream Yok ❤️</span>
      <div className="flex items-center gap-3">
        <img
          src="/api/placeholder/40/40"
          alt="Mupcop logo"
          className="w-8 h-8 object-contain rounded-lg"
        />
        <span className="text-lg font-bold text-gray-800">Mupcop</span>
      </div>
    </div>
  </footer>
);

const MupcopDashboard = () => {
  return (
    <div className="h-screen flex flex-col bg-white">
      <Header />
      <div className="flex-1 flex overflow-hidden">
        <MonitoringPanel />
        <MapView />
      </div>
      <Footer />
    </div>
  );
};

export default MupcopDashboard;